.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    text-transform: uppercase;
    background-position: center;
    background-size: cover;
    overflow: hidden;
  
    &.large{
      height: 380px;
    }

    &:hover{
      cursor: pointer;
      
      .bc-img{
        transition: 6s all;
        transform: scale(1.1);
      }

      .content{
        opacity: 0.9;
      }
    }

    .bc-img{
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0px;
      padding: 0px;
      
    }
    .content {
      height: 90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background-color: white; 
      opacity: 0.5;
      position: absolute;
    
  
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #4a4a4a;
      }
  
      .subtitle {
        font-weight: lighter;
        font-size: 16px;
      }
    }
  }